.slider {
  position: relative; }
  .slider__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75px;
    position: absolute;
    top: 0;
    bottom: 0;
    transition: all .4s ease-in; }
    .slider__arrow:hover svg, .slider__arrow:focus svg, .slider__arrow:active svg {
      transform: scale(1.3); }
    .slider__arrow--left {
      left: 0;
      transition: all .4s ease-in; }
      .slider__arrow--left svg {
        transform: rotate(180deg); }
      .slider__arrow--left:hover svg, .slider__arrow--left:focus svg, .slider__arrow--left:active svg {
        transform: rotate(180deg) scale(1.3); }
    .slider__arrow--right {
      right: 0; }
    .slider__arrow svg {
      fill: #ffcc00;
      transition: all .4s ease-in; }
  .slider__dots {
    padding: 30px;
    margin: 0;
    text-align: center; }
    .slider__dots li {
      display: inline-block; }
  .slider__dot {
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 1px solid transparent;
    background-color: #ffcc00;
    transition: all .4s ease-in; }
    .slider__dot--active {
      border-color: #ffcc00;
      background-color: transparent; }
    .slider__dot:hover, .slider__dot:focus, .slider__dot:active {
      border-color: #ffcc00;
      background-color: transparent; }

* {
  box-sizing: border-box; }

html {
  height: 100%;
  font-size: 16px; }

body {
  min-height: 100%;
  font: 1.5rem/1.6  'Open Sans', sans-serif; }

.container {
  min-width: 1140px;
  margin: 0 auto;
  width: 96%; }
